/* üí† General */
.App {
  text-align: center;
}

.App-header {
  background: linear-gradient(135deg, #f6ffd7, #d8f9b0);
  min-height: 100%;
  max-width: 100%;
  width: 100%;
  padding-top: 15%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin);
  color: #222;
  width: 100%;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  border-radius: 15px;
}

/* üìå Men√∫ Superior */
.menu_superior {
  position: fixed;
  top: 0;
  left: 0;
  
  padding-bottom: 1%;
  width: 100%;
  background: linear-gradient(90deg, #afe66d, #78c21f);
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap; /* ‚úÖ Evita desbordes en pantallas peque√±as */
}

.datos_menu_superior a {
  font-size: 18px;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
  text-decoration: none;
  padding: 10px 15px;
  border-radius: 8px;
  transition: background 0.3s ease;
}

.datos_menu_superior a:hover {
  background: rgba(255, 255, 255, 0.3);
}

/* üé® Contenedores con bordes y efectos */
/* üìå Contenedor de productos */
.contenedor {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
  justify-content: center;
  padding: 20px;
  width: 100%;
  max-width: 1200px;
}

/* üîπ Bloques de productos con dise√±o din√°mico */
.bloque-productos {
  width: 100%;
  max-width: 260px;
  height: auto;
  padding: 20px;
  background: linear-gradient(180deg, #d8f9b0, #afe66d);
  border-radius: 12px;
  border: 3px solid #78c21f;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* üèÜ Efecto al pasar el mouse */
.bloque-productos:hover {
  transform: scale(1.05);
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.35);
}

/* üìå Imagen circular */
.img-circular {
  width: 150px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
}

/* üìå Modificaci√≥n de precio */
.mod-precio {
  font-size: 14px;
  border: 1px solid #222;
  color: #ffffff;
  padding: 5px;
}

/* üèÜ Men√∫ Inferior */
.menu-inferior {
  width: 100%;
  background: #78c21f;
  color: white;
  padding: 15px;
  text-align: center;
  font-size: 16px;
  font-weight: bold;
  box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.3);
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

/* üåü Animaciones */
@keyframes glow {
  0% { box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); }
  50% { box-shadow: 0px 4px 15px rgba(255, 255, 255, 0.3); }
  100% { box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2); }
}

.App-header:hover {
  animation: glow 2s infinite alternate;
}

/* üîπ Ajustes para m√≥viles */
@media screen and (max-width: 600px) {
  .menu_superior {
    flex-direction: column;
    text-align: center;
    padding: 5px;
  }

  .contenedor {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    padding: 10px;
  }

  .bloque-productos {
    max-width: 180px;
    padding: 10px;
  }

  .mod-precio {
    font-size: 12px;
    padding: 3px;
  }
}
